<template>
  <div>
    <div class="box">
      <img
        src="../assets/icon.png"
        alt=""
      >
      <Minput
        type='text'
        text='邮箱'
        v-model="user.email"
      ></Minput>
      <Minput
        type='password'
        text='密码'
        v-model="user.password"
      ></Minput>

      <Minput
        type='password'
        text='确认密码'
        v-model="user.confirmPassword"
      ></Minput>

      <div class="input">
        <button @click='handle'>注册</button>
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "../components/Nav";
import Minput from "../components/commen/Minput";
import { mkdir } from "fs";
export default {
  components: {
    Nav,
    Minput
  },
  data() {
    return {
      user: {
        email: "",
        password: "",
        confirmPassword: ""
      }
    };
  },
  methods: {
    handle() {
      console.log(this.user);

      this.$axios
        .post("/users.json", this.user)
        .then(res => {
          console.log(res);
          this.$router.push("/login");
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.box {
  width: 90%;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 0 auto;
  margin-top: 10px;
  padding: 20px;
}
.box img {
  display: block;
  width: 256px;
  margin: 0 auto;
}
.input {
  width: 100%;
}
.input button {
  border: 0;
  border-color: #28a745;
  background-color: #28a745;
  height: 36px;
  border-radius: 5px;
  padding: 6px 12px;
  width: 100%;
  color: #fff;
  margin-top: 20px;
}
</style>